<template>
    <div
        class="m-search-bar"
        :class="$route.name == 'plan_view' ? 'can-return' : ''"
    >
        <div class="m-return">
            <el-button class="u-return-btn" @click="return_handle"
                >返回</el-button
            >
        </div>
        <div class="m-search">
            <el-input
                class="u-search-input"
                v-model="keyword"
                @keydown.enter.native="search_handle"
                placeholder="输入物品清单名称「回车」进行搜索"
            >
                <span slot="prepend">关键词</span>
                <el-button
                    slot="append"
                    class="u-search-btn"
                    type="primary"
                    plain
                    @click="search_handle"
                    >搜索</el-button
                >
            </el-input>
        </div>
        <div class="m-add">
            <el-button
                class="u-add-btn"
                @click="add_handle"
                type="primary"
                icon="el-icon-plus"
                >创建清单</el-button
            >
        </div>
    </div>
</template>

<script>
import {__Links} from "@jx3box/jx3box-common/data/jx3box.json";

export default {
    name: "PlanSearch",
    data() {
        return {
            keyword: "",
        };
    },
    methods: {
        return_handle() {
            if (history.length <= 1) {
                this.$router.push({ name: "plan_list" });
            } else {
                history.back();
            }
        },
        add_handle() {
            location.href = `${__Links.dashboard.publish}#/item/plan`;
        },
        search_handle() {
            this.$router.push({
                name: "plan_list",
                params: { keyword: this.keyword },
            });
        },
    },
};
</script>

<style lang="less">
@import "../assets/css/components/search.less";
.m-search-bar {
    .pr;
    justify-items: center;
}

.m-search {
    .mr(130px);
}

.m-add {
    .pa;
    top: 14px;
    right: 0;
}

@media screen and (max-width: @ipad) {
    .m-search {
        .mr(0);

        .el-input-group__prepend {
            .none;
        }
    }

    .m-add {
        .ps;
        .mt(10px);

        .el-button {
            .w(100%);
        }
    }
}
</style>
